@charset "UTF-8";

$success: #41B314;
$warning:  #e4cb10; 
$danger:  #F9354C; 
$blue:  #00AAFF;
$myMargin: 0px auto;
$f-14: 14px;

/* RESET CSS */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
 }
html { 
   position: relative; 
    min-height: 100%;
  }

body { 
  background: #ffffff;
  font-family: 'Source Sans Pro';
   font-weight: 400; 
   color:#333333; 
   font-size: 16px; 
   line-height: 140%;
  }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}
 
ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

blockquote, q {
  quotes: none;
}

blockquote {
  &:before, &:after {
    content: '';
    content: none;
  }
}

q {
  &:before, &:after {
    content: '';
    content: none;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* RESET CSS */

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  &:hover {
    background: #555;
  }
}

html {
  position: relative;
  min-height: 100%;
}

body {
  background-color: #F3F5F8;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 15px;
  color: #676a6d;
}

a {
  color: #676a6d;
  &:hover, &:focus {
    color: #676a6d;
    text-decoration: none;
  }
}

.page-title {
  margin-top: 0;
  margin-bottom: 30px;
  font-weight: 300;
}

.top-5 {
  top: -5px !important;
}

.btn {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 1px 2px 0 rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 1px 2px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0px 1px 2px 0 rgba(0, 0, 0, 0.2);
  padding: 6px 22px;
}

#wrapper {
  .sidebar {
    width: 260px;
    height: 100%;
    float: left;
    background-color: #2B333E;
    position: fixed;
    left: 0;
    overflow: auto;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .main {
    width: calc(100% - 260px);
    left: 0;
    position: relative;
    float: right;
    background-color: #f5f5fa;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  #sidebar-nav {
    padding-top: 80px;
  }
}

@media screen and (max-width: 767px) {
  #wrapper {
    #sidebar-nav, .main {
      padding-top: 130px;
    }
  }
}
 
.layout-fullwidth #wrapper {
  .sidebar {
    left: -260px;
  }
  .main {
    width: 100%;
  }
  .navbar {
    left: 0 !important;
  }
  .btn-toggle-fullwidth {
    color: #00AAFF;
  }
}

.offcanvas-active #wrapper {
  .sidebar {
    left: 0;
  }
  .main {
    width: 100%;
    position: relative;
    left: 260px;
  }
}

@media screen and (max-width: 1024px) {
  #wrapper {
    .sidebar {
      left: -260px;
    }
    .main {
      width: 100%;
    }
    .navbar {
      left: 0 !important;
    }
    .btn-toggle-fullwidth {
      color: #00AAFF;
    }
  }
  .btn-toggle-minified {
    display: none;
  }
}

.main-content {
  padding: 28px 10px;
}

.navbar + .main-content {
  padding: 105px 0 28px 5px;
}

@media screen and (max-width: 767px) {
  .navbar + .main-content {
    padding-top: 155px;
  }
}

html.fullscreen-bg {
  height: 100%;
  body, #wrapper {
    height: 100%;
  }
}

.vertical-align-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  display: table;
}

.vertical-align-middle {
  display: table-cell;
  vertical-align: middle;
}

.navbar-default {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  padding: 0;
  margin: 0;
  border: none;
  background-color: #fff;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -moz-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  .navbar-nav > .open > a {
    background-color: #fafafa;
    &:hover, &:focus {
      background-color: #fafafa;
    }
  }
  .navbar-toggle {
    background-color: #f8f8f8;
    &:hover, &:focus {
      background-color: #f8f8f8;
    }
  }
  .brand {
    float: left;
    padding: 30px 39px;
    background-color: #fff;
  }
}

@media screen and (max-width: 1024px) {
  .navbar-default .brand {
    padding-left: 30px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 640px) {
  .navbar-default #navbar-menu {
    float: right;
  }
  .navbar-btn {
    padding-left: 0;
  }
  .navbar-form {
    display: none;
  }
}



.navbar-btn {
  float: left;
  padding: 16px 0;
  button {
    font-size: 24px;
    padding: 4px 8px;
    line-height: .7;
    border: none;
    background: none;
    outline: none;
  }
}
013
.navbar-btn-right {
  float: right;
}

.navbar-form {
  padding: 15px;
  border: 0;
  input[type="text"] {
    min-width: 150px;
  }
}

@media screen and (max-width: 767px) {
  .navbar-form {
    float: left;
    width: 70%;
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 0;
    padding-right: 0;
    .input-group {
      width: 70%;
    }
    input[type="text"] {
      min-width: 150px;
    }
  }
}

.navbar-nav {
  .icon-menu {
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    *zoom: 1;
    *display: inline;
    padding-top: 28px;
    padding-bottom: 28px;
    position: relative;
    .badge {
      position: absolute;
      top: 22px;
      right: 16px;
    }
   }
  > li > {
    a {
      padding: 28px 20px;
      > {
        i {
          font-size: 22px;
          margin-right: 2px;
          vertical-align: middle;
          &.icon-submenu {
            margin-left: 2px;
            margin-right: 0;
            font-size: 10px;
            position: relative;
            top: 2px;
          }
        }
        span {
          position: relative;
          top: 2px;
        }
      }
      img {
        width: 22px;
        margin-right: 2px;
      }
    }
    .dropdown-menu {
      padding: 0;
      min-width: 200px;
    }
  }
  .dropdown-menu {
    border-top: 2px solid #ACB0B5;
    > li > a {
      padding: 10px 20px;
      &:hover, &:focus {
        background-color: #fafafa;
      }
      span, i {
        vertical-align: middle;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .navbar-nav.navbar-right {
    margin-right: 0;
  }
}

@media screen and (max-width: 767px) {
  .navbar-nav {
    width: 100%;
    margin: 0;
    > li {
      display: table-cell;
      position: inherit;
      text-align: center;
      > a {
        padding: 15px 20px;
        span:not(.badge), .icon-submenu {
          display: none;
        }
      }
    }
    .open .dropdown-menu {
      -moz-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
      -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
      position: absolute;
      width: 100%;
      margin-top: 3px;
      background-color: #fff;
      > li > a {
        padding: 10px 15px;
      }
    }
    .icon-menu {
      padding-top: 15px;
      padding-bottom: 15px;
      .badge {
        top: 10px;
        right: 15px;
      }
    }
  }
}

.navbar-toggle {
  padding: 5px 8px;
  margin: 20px 15px 20px 0;
  .icon-nav {
    font-size: 24px;
  }
}

.dropdown-menu {
  -moz-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  border-color: #eaeaea;
  > li {
    > a {
      color: #676a6d;
    }
    i {
      margin-right: 6px;
    }
  }
  .active > a {
    background-color: #00AAFF;
    &:focus, &:hover {
      background-color: #00AAFF;
    }
  }
  > .disabled > a {
    color: #a3a3a3;
    &:focus, &:hover {
      color: #a3a3a3;
    }
  }
}

/* Sidebar Navigation */

.sidebar {
  z-index: 9;
  .nav {
    > li > a {
      padding: 18px;
      color: #AEB7C2;
      border-left: 5px solid transparent;
      position: relative;
      display: table;
      width: 100%;
      &:hover, &:focus, &.active {
        color: #fff;
        background-color: transparent;
      }
      &:hover i, &:focus i, &.active i {
        color: #00AAFF;
      }
      &:focus, &.active {
        background-color: #252c35;
        border-left-color: #00AAFF;
      }
      &[data-toggle="collapse"] {
        .icon-submenu {
          display: inline-block;
          vertical-align: middle;
          vertical-align: auto;
          zoom: 1;
          display: inline;
          -webkit-transition: all 0.2s ease-out;
          -moz-transition: all 0.2s ease-out;
          -ms-transition: all 0.2s ease-out;
          -o-transition: all 0.2s ease-out;
          transition: all 0.2s ease-out;
          float: right;
          position: relative;
          top: 5px;
          font-size: 12px;
          line-height: 1.1;
          -moz-transform: rotate(-90deg);
          -ms-transform: rotate(-90deg);
          -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
        }
        &.collapsed .icon-submenu {
          -moz-transform: rotate(0deg);
          -ms-transform: rotate(0deg);
          -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
        }
      }
      .badge {
        font-weight: 400;
        background-color: #F9354C;
      }
    }
    i {
      margin-right: 10px;
      font-size: 18px;
    }
    span {
      -webkit-transition: all 0.3s ease-in-out;
      -moz-transition: all 0.3s ease-in-out;
      -ms-transition: all 0.3s ease-in-out;
      -o-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out;
      position: relative;
      top: -2px;
    }
    .nav {
      background-color: #252c35;
      > li > a {
        padding-left: 60px;
        padding-top: 10px;
        padding-bottom: 10px;
        &:focus {
          background-color: transparent;
          border-left-color: transparent;
        }
        &.active {
          background-color: transparent;
          border-left-color: transparent;
          a {
            color: #fff;
          }
        }
      }
    }
  }
}

/* submenu */

/* PANELS */

.panel {
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  -moz-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  background-color: #fff;
  margin-bottom: 30px;
  .panel-heading, .panel-body, .panel-footer {
    padding-left: 25px;
    padding-right: 25px;
  }
  .panel-heading {
    padding-top: 20px;
    padding-bottom: 20px;
    position: relative;
    .panel-title {
      margin: 0;
      font-size: 18px;
      font-weight: 300;
    }
    button {
      padding: 0;
      margin-left: 5px;
      background-color: transparent;
      border: none;
      outline: none;
      i {
        font-size: 14px;
      }
    }
  }
  .panel-body {
    padding-top: 10px;
    padding-bottom: 15px;
  }
  .panel-note {
    font-size: 13px;
    line-height: 2.6;
    color: #777777;
    i {
      font-size: 16px;
      margin-right: 3px;
    }
  }
  .right {
    position: absolute;
    right: 20px;
    top: 32%;
  }
  &.panel-headline .panel-heading {
    border-bottom: none;
    .panel-title {
      margin-bottom: 8px;
      font-size: 22px;
      font-weight: normal;
    }
    .panel-subtitle {
      margin-bottom: 0;
      font-size: 14px;
      color: #8D99A8;
    }
  }
  &.panel-scrolling .btn-bottom {
    margin-bottom: 30px;
  }
  .table > {
    thead > tr > {
      td:first-child, th:first-child {
        padding-left: 25px;
      }
    }
    tbody > tr > {
      td:first-child, th:first-child {
        padding-left: 25px;
      }
    }
    tfoot > tr > {
      td:first-child, th:first-child {
        padding-left: 25px;
      }
    }
    thead > tr > {
      td:last-child, th:last-child {
        padding-left: 25px;
      }
    }
    tbody > tr > {
      td:last-child, th:last-child {
        padding-left: 25px;
      }
    }
    tfoot > tr > {
      td:last-child, th:last-child {
        padding-left: 25px;
      }
    }
  }
}

.panel-footer {
  background-color: #fafafa;
}

.collapsing {
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.metric {
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  padding: 20px;
  margin-bottom: 30px;
  border: 1px solid #DCE6EB;
  .icon {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    float: left;
    width: 50px;
    height: 50px;
    line-height: 50px;
    background-color: #0081c2;
    text-align: center;
    i {
      font-size: 22px;
      color: #fff;
      position: relative;
      top: 3px;
    }
  }
  p {
    margin-bottom: 0;
    line-height: 1.2;
    text-align: right;
  }
  .number {
    display: block;
    font-size: 28px;
    font-weight: 300;
  }
  .title {
    font-size: 16px;
  }
}

ul.activity-list > li {
  padding: 25px 0;
  &:not(:last-child) {
    border-bottom: 1px solid #F0F0F0;
  }
  .usericon {
    width: 40px;
  }
  > p {
    margin-bottom: 0;
    padding-left: 3.5em;
  }
  .timestamp {
    display: block;
    font-size: 13px;
    color: #a3a3a3;
  }
}

footer {
  position: absolute;
  bottom: 0;
  right: 0;
  padding-top: 15px;
  padding-bottom: 10px;
}

.copyright {
  margin-bottom: 0;
  text-align: right;
  font-size: 13px;
}

@media screen and (max-width: 767px) {
  .copyright {
    text-align: center;
    position: relative !important;
  }
}

.bg-success {
  background-color: #41B314;
}

.bg-warning {
  background-color: #e4cb10;
}

.bg-danger {
  background-color: #F9354C;
}

.bg-blue {
  background-color: #00AAFF;
}

.no-padding {
  padding: 0 !important;
}